// 
Quote.prototype = {
    constructor: Quote,

    onClick: function onClick(e) {
        var editor = this.editor;
        var $selectionElem = editor.selection.getSelectionContainerElem();
        var nodeName = $selectionElem.getNodeName();
        var range = editor.selection.getRange();
        var start = range.startOffset;
        var end = range.endOffset;
        // console.log(nodeName, start, end);

        // if (!UA.isIE()) {
        //     if (nodeName === 'BLOCKQUOTE') {
        //         //  quote
        //         editor.cmd.do('formatBlock', '<P>')
        //     } else {
        //         //  quote
        //         editor.cmd.do('formatBlock', '<BLOCKQUOTE>')
        //     }
        //     return
        // }
        // return

        // IE  formatBlock <BLOCKQUOTE> 
        var content = void 0,
            $targetELem = void 0;
        if (nodeName === 'P' || nodeName === 'H1' || nodeName === 'H2') {
            //  P  quote
            content = $selectionElem.text();
            $targetELem = $('<blockquote>' + content + '</blockquote>');
            $targetELem.insertAfter($selectionElem);
            $selectionElem.remove();
        } else if (nodeName === 'BLOCKQUOTE') {
            //  quote
            content = $selectionElem.text();
            $targetELem = $('<p>' + content + '</p>');
            $targetELem.insertAfter($selectionElem);
            $selectionElem.remove();
        }
        if (!$targetELem) {
            return;
        }
        // console.log(content, content.length - 1, $targetELem, '');
        editor.selection.setSelectionStart($targetELem[0].firstChild, start);
        editor.selection.setSelectionEnd($targetELem[0].firstChild, end);

        editor.selection.restoreSelection();

        // console.log(editor.selection.getRange(), '');
    },

    tryChangeActive: function tryChangeActive(e) {
        var editor = this.editor;
        var $elem = this.$elem;
        var reg = /^BLOCKQUOTE$/i;
        var cmdValue = editor.cmd.queryCommandValue('formatBlock');
        if (reg.test(cmdValue)) {
            this._active = true;
            $elem.addClass('w-e-active');
        } else {
            this._active = false;
            $elem.removeClass('w-e-active');
        }
    }
};

